import React from 'react';
import { Formik, Form } from 'formik';
import { TextField } from './TextField';
import * as Yup from 'yup';
import "./Signup.css"


export const Signup = () => {
  const validate = Yup.object({
    firstName: Yup.string()
      .max(15, 'Must be 15 characters or less')
      .required('Required'),
    lastName: Yup.string()
      .max(20, 'Must be 20 characters or less')
      .required('Required'),
    email: Yup.string()
      .email('Email is invalid')
      .required('Email is required'),
    password: Yup.string()
      .min(6, 'Password must be at least 6 charaters')
      .required('Password is required'),
    confirmPassword: Yup.string()
      .oneOf([Yup.ref('password'), null], 'Password must match')
      .required('Confirm password is required'),
  })
  return (
    <Formik
      initialValues={{
        firstName: '',
        lastName: '',
        email: '',
        password: '',
        confirmPassword: ''
      }}
      validationSchema={validate}
      onSubmit={values => {
        console.log(values)
      }}
    >
      {formik => (
        <div className="loggin">
          <h1 className="my-4 font-weight-bold .display-4">Sign Up</h1>
          <Form>
            <p>Enter youre name</p>
            <TextField  autoComplete="none" name="firstName" type="text" className="Emmail" />
            <p>last Name</p>

            <TextField  name="lastName" type="text"className="Emmail" />
            <p>Email</p>
            <TextField  name="email" type="email" className="Emmail"/>
            <p>password</p>

            <TextField  name="password" type="password"className="Emmail" />
            <p>Confirm Password</p>


            <TextField  name="confirmPassword" type="password"className="Emmail" />
            <br/>
            <div className="gend">
            <p> <input type="radio" id="male" name="gender" value="male"/>male</p>
            <p> <input type="radio" id="male" name="gender" value="male"/>Female</p>
            <br/>
              <p><input type="date"/></p>
            </div>
            <br/>

            <button className="bttttn" type="submit">Register</button>
            <button className="bttttn" type="reset">Reset</button>
          </Form>
        </div>
      )}
    </Formik>
  )
}


